
import { defineComponent } from 'vue';
const moment = require("moment");

export default defineComponent({
  name: "PostCard",
  props: {
    id: String,
    title: String,
    subtitle: String,
    content: String,
    previewImg: String,
    writer: String,
    date: {type: String,
    required: true}
  },
  data() {
    return {
      postUrl: "/post/" + this.id + "/",
    };
  },
  methods: {
    getImgUrl() {
      return this.previewImg
    },
    getFormattedDateTime(date: String) {
      return moment(date).format("DD MMM YYYY");
    }
  },
});
